<script>
    let transform;

    export let holdFlip;

    function flipCard(){
        if(holdFlip)
            return;

        if(transform)
            transform = '';
        else
            transform = 'transform: rotateX(180deg);';
    }
</script>

<style>
    .card-scene{
        height: 180px;
        width: 800px;
        perspective: 600px;
    }

	.lx-card {
        background-color: #2a2c37;
        cursor: pointer;
        width: 100%;
        height: 100%;
        transition: transform 1s;
        transform-style: preserve-3d;
        cursor: pointer;
        position: relative;
	}

    .back-side, .front-side{
        position: absolute;
        width: 100%;
        height: 100%;
        -webkit-backface-visibility: hidden; /* Safari */
        backface-visibility: hidden;
        left: 0px;
        top: 0px;
        display: flex;
        align-items:center;
        justify-content:center;
    }
    .back-side{
        transform: rotateX(180deg);
    }
    .front-side{
        transform: rotateX(0deg);
    }

</style>

<div class="card-scene">
    <div class="lx-card" on:click={flipCard} style="{transform}">
        <div class="front-side">
            <slot name="front">
                
            </slot>
        </div>
        <div class="back-side">
            <slot name="back">

            </slot>
        </div>
    </div>
</div>


